<script>
  import { navigate } from 'svelte-routing';
  import { onMount } from 'svelte';

  let showMenu = false;
  let dropdowns = {
    '#about': false,
    '#committees': false
  };

  function handleClick(event) {
    const href = event.currentTarget.getAttribute('href');
    const isDropdown = event.currentTarget.classList.contains('dropbtn');

    if (!isDropdown) {
      event.preventDefault();
      dropdowns = {
        '#about': false,
        '#committees': false
      };

      if (href.startsWith('http')) {
        window.open(href, '_blank');
      } else {
        navigate(href);
        if (window.innerWidth <= 768) {
          showMenu = false;
        }
      }
    } else {
      event.preventDefault();
      const dropdownId = event.currentTarget.getAttribute('href');
      dropdowns = {
        '#about': false,
        '#committees': false,
        [dropdownId]: !dropdowns[dropdownId]
      };
    }
  }

  function toggleMenu() {
    showMenu = !showMenu;
    if (!showMenu) {
      dropdowns = {
        '#about': false,
        '#committees': false
      };
    }
  }

  function handleKeydown(event) {
    if (event.key === 'Enter' || event.key === ' ') {
      event.preventDefault();
      toggleMenu();
    }
  }

  function handleClickOutside(event) {
    if (showMenu && !event.target.closest('.navbar')) {
      showMenu = false;
    }
    if (!event.target.closest('.dropdown')) {
      dropdowns = {
        '#about': false,
        '#committees': false
      };
    }
  }

  onMount(() => {
    document.addEventListener('click', handleClickOutside);

    return () => {
      document.removeEventListener('click', handleClickOutside);
    };
  });
</script>

<style>
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    cursor: none;
  }

  .navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(51, 51, 51, 0);
    color: white;
    padding: 1vw 2vw;
    position: relative;
    font-family: 'Libre', serif;
    font-weight: bold;
    font-size: 1.5vw;
    z-index: 1000;
  }

  .burger-menu {
    display: none;
    cursor: pointer;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 6vw;
    height: 4vw;
    position: fixed;
    right: 2vw;
    top: 1.5vw;
    z-index: 1200;
  }

  .burger-menu div {
    width: 100%;
    height: 0.8vw;
    background-color: white;
    margin: 0.4vw 0;
    transition: transform 0.3s ease, opacity 0.3s ease;
  }

  .menu-items {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .menu-items a {
    color: white;
    padding: 1.4vw 2vw;
    text-decoration: none;
    position: relative;
  }

  .menu-items a:hover {
    transition: transform 0.3s;
    transform: scale(1.1);
    background-color: rgba(85, 85, 85, 0.74);
    border-radius: 1vw;
  }

  .dropdown:hover .dropbtn {
    background-color: rgba(85, 85, 85, 0.74);
    border-radius: 1vw;
  }

  .dropdown {
    position: relative;
    display: inline-block;
    text-align: center;
  }

  .dropbtn {
    display: flex;
    align-items: center;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #222;
    min-width: 16vw;
    box-shadow: 0 0.8vw 1.6vw rgba(0, 0, 0, 0.2);
    z-index: 1;
    margin-top: 0;
    font-size: 1vw;
    border-radius: 1vw;
    word-spacing: 0.3vw;
  }

  .dropdown-content a {
    color: white;
    padding: 1.2vw 1.6vw;
    text-decoration: none;
    display: block;
    text-align: center;
    position: relative;
  }

  .dropdown-content a:hover {
    background-color: rgba(70, 70, 70, 0);
  }

  .dropdown-content.show {
    display: block;
  }

  @media screen and (max-width: 768px) {
    .burger-menu {
      display: block;
    }

    .menu-items {
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      height: 100vh;
      width: 100%;
      background-color: rgba(0, 0, 0, 0.8);
      z-index: 1100;
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.1s ease, transform 0.1s ease;
      gap: 1vw;
      padding-top: 6vw;
      padding-bottom: 5vw;
    }

    .menu-items.show {
      display: flex;
      opacity: 1;
      pointer-events: auto;
      flex-direction: column;
      animation: fall 0.3s ease-out forwards;
    }

    .menu-items a {
      padding: 6vw 2vw;
      text-decoration: none;
      color: white;
      display: block;
      transition: background-color 0.3s ease;
      font-size: 5vw;
    }

    .menu-items a:hover {
      background-color: #555;
    }

    .burger-menu div {
      width: 5vw;
      height: 0.6vw;
      background-color: white;
      margin: 0.8vw auto;
      transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
    }

    .burger-menu.active div:nth-child(1) {
      transform: rotate(-45deg) translate(-1vw, 1vw);
    }

    .burger-menu.active div:nth-child(2) {
      opacity: 0;
    }

    .burger-menu.active div:nth-child(3) {
      transform: rotate(45deg) translate(-1vw, -1vw);
    }

    .dropdown {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-right: 1vw;
      align-self: center;
    }

    .menu-items a:hover {
      transform: scale(1.1);
      background-color: rgba(85, 85, 85, 0.27);
      border-radius: 1vw;
    }

    .dropdown-content {
      display: none;
      position: relative;
      background-color: #222;
      min-width: 100%;
      box-shadow: none;
      z-index: 1;
      margin-top: 2vw;
      font-size: 1.5vw;
      right: 0;
      left: 50%;
      transform: translateX(-50%);
    }

    .dropdown-content a {
      text-align: center;
      font-size: 4vw;
    }

    .dropdown-content.show {
      display: block;
    }

    /*.hide-on-mobile {
      display: none !important;
    }*/
  }

  @keyframes fall {
    0% {
      transform: translateY(-100%);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
</style>

<div class="navbar">
  <div class="menu-items {showMenu ? 'show' : ''}">
    <a href="/" on:click={handleClick}>Home</a>
    <div class="dropdown">
      <a href="#about" class="dropbtn" on:click={handleClick}>About</a>
      <div class="dropdown-content {dropdowns['#about'] ? 'show' : ''}">
        <a href="/AboutBoscoMUN" on:click={handleClick}>About BoscoMUN</a>
        <a href="/PrincipalAddress" on:click={handleClick}>Rev. Fr. Rector and Principal's Address</a>
        <a href="/SecGenMessage" on:click={handleClick}>Secretary General's Message</a>
        <a href="/DepSecGenMessage" on:click={handleClick}>Deputy Secretary General's Message</a>
      </div>
    </div>
    <div class="dropdown">
      <a href="#committees" class="dropbtn" on:click={handleClick}>Committees</a>
      <div class="dropdown-content {dropdowns['#committees'] ? 'show' : ''}">
        <a href="/UNSC" on:click={handleClick}>United Nations Security Council</a>
        <a href="/AIPPM" on:click={handleClick}>All India Political Parties Meet</a>
        <a href="/JSC" on:click={handleClick}>Joseph Stalin’s Cabinet</a>
        <a href="/ASEAN" on:click={handleClick}>Association of Southeast Asian Nations</a>
        <a href="/AL" on:click={handleClick}>Arab League</a>
        <a href="/IPC" on:click={handleClick}>International Press Corps</a>
      </div>
    </div>
    <a href="/Resources" on:click={handleClick}>Resources</a>
    <!--<a href="/IndividualRegistration" class="hide-on-mobile" on:click={handleClick}>Individual Registration</a>-->
    <a href="/OurTeam" on:click={handleClick}>Our Team</a>
  </div>
  <div
    class="burger-menu {showMenu ? 'active' : ''}"
    tabindex="0"
    role="button"
    aria-label="Toggle menu"
    on:click={toggleMenu}
    on:keydown={handleKeydown}
  >
    <div></div>
    <div></div>
    <div></div>
  </div>
</div>
