<script>
  let items = [
    { img: '/UNSC.png', pdf: '/pdfs/UNSC.pdf' },
    { img: '/AIPPM.png', pdf: '/pdfs/AIPPM.pdf' },
    { img: '/JSC.png', pdf: '/pdfs/JSC.pdf' },
    { img: '/ASEAN.png', pdf: '/pdfs/ASEAN.pdf' },
    { img: '/AL.png', pdf: '/pdfs/AL.pdf' },
    { img: '/IPC.png', pdf: '/pdfs/IPC.pdf' }
  ];

  let items2 = [
    { img: '/handbook.png', pdf: '/pdfs/BOSCO_MUN_CONFERENCE_HANDBOOK.pdf' },
    { img: '/schedule.png', pdf: '/pdfs/BoscoMUN24_Conference_Schedule_and_Venues.pdf'}
  ];

  let items3 = [
    { img: '/JSC.png', pdf: '/pdfs/JSCPORTFOLIO.pdf' },
  ];

  function downloadPDF(pdfUrl) {
    const link = document.createElement('a');
    link.href = pdfUrl;
    link.setAttribute('download', '');
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
</script>

<h1 class="gallery-heading">Study Guides</h1>
<div class="gallery">
  {#each items as item}
    <div class="item" on:click={() => downloadPDF(item.pdf)}>
      <img src={item.img} alt="Study Guides" />
    </div>
  {/each}
</div>

<h1 class="gallery-heading">Resources</h1>
<div class="gallery">
  {#each items2 as item2}
    <div class="item" on:click={() => downloadPDF(item2.pdf)}>
      <img src={item2.img} alt="Resources" />
    </div>
  {/each}
</div>

<h1 class="gallery-heading">Portfolio Guides</h1>
<div class="gallery">
  {#each items3 as item3}
    <div class="item" on:click={() => downloadPDF(item3.pdf)}>
      <img src={item3.img} alt="Portfolios" />
    </div>
  {/each}
</div>

<style>
  .gallery-heading {
    text-align: center;
    margin-bottom: 2vw;
    font-size: 3vw;
    color: white;
    font-family: serif;
  }

  .gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 3vw;
    padding: 2vw;
    max-width: 70vw;
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
  }

  .item {
    flex: 1 1 calc(33.33% - 5vw);
    max-width: calc(33.33% - 5vw);
    border-radius: 1vw;
    overflow: hidden;
    transition: transform 0.3s;
    margin: 0;
    padding: 0;
    cursor: pointer;
  }

  .item:hover {
    transform: scale(1.05);
  }

  .item img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
    margin: 0;
    padding: 0;
  }

  @media (max-width: 768px) {
    .item {
      flex: 1 1 80vw;
      max-width: 80vw;
    }
  }

  @media (min-width: 769px) and (max-width: 1200px) {
    .item {
      flex: 1 1 calc(50% - 3vw);
      max-width: calc(50% - 3vw);
    }
  }

  @media (max-width: 768px) {
    .gallery-heading {
      font-size: 10vw;
    }
  }
</style>
