    <br>
    <p class="address-section"><strong class="highlight-underline">About the Committee-</strong></p>
    <p>Amidst chaos from around the world, it falls among our pinnacle duties to protect the people who provide us with the happenings around the world. A world without the Press is a world without knowledge. It is the freedom of the press that makes it formidable in its power, being able to challenge and argue against any decision of a higher power from the viewpoint of the common citizen.
    </p>
    <p>In these times of disorder, a press that is free, a press that is powerful, and a press that is trustworthy is of significant importance. Delegates have been given the foremost task of determining solutions regarding the problem of censored journalism and the severe lack of protection of the journalists in the fatal regions of war, while also covering the conference and providing all such accurate reports about their endeavour here.
    </p>
    <p>Engage yourselves and rise to the challenge of upholding the imperative task of free journalism, while remembering about your safety and that the "Truth shalt not be hidden".      
    </p><br>
    <p><strong class="highlight" >Agenda – Freedom of the Press and Protection of the Journalists in conflict zones and the general coverage of the conference. </strong></p>
    <br>
    <p class="address-section"><strong class="highlight-underline">Press Head's Address-</strong></p>
    <p>Greetings Delegates,    </p>
    <p>It is with great pleasure that I welcome you to a committee which is a first in the history of Bosco MUN, the International Press Corps. In this unique committee, we will be debating upon press freedom and the safety and protection of press correspondents in conflict zones.</p>
    <p>The mere introduction of such a committee for the very first time at an established MUN like Bosco MUN should serve as a testament to the importance of the same. In such difficult times, the role of the press is becoming increasingly profound, with it playing a central role in dispersing important information about controversial and sensitive issues with utmost ruthlessness and sincerity.</p>
    <p>In chairing this committee I will be assisted by the Deputy Press Head, Himank More, a 12th grader who is an avid and enthusiastic debater and the only thing he loves more than bending his own and others’ policy, is food. An approachable guy with immense problem-solving skills, he is almost always seen with a wide smile across his face.
    </p>
    <p>Riddhiman Gangopadhyay, the Press Head, is also a 12th grader commerce student with large dreams and an ever-present curiosity about finance, economics and the business world. Although he is more often seen at business conferences (or on FIFA), he finds the concept of MUNs fascinating and considers it a way to sharpen his oratory and research skills.
      </p>
    <p>Now, as highlighted before, the expectations from the delegates in this committee are very high. As is becoming essential, each and every delegate present will represent large and well-reputed press agencies and have to accordingly maintain extremely high ethical and moral standards and adhere to the highest grade of integrity and righteousness. Research holds great importance in this committee, but your value, rationality and ability to articulate and present smoothly and seamlessly. Of course, the written word will hold paramount significance and every single word that is put into an article will be carefully and meticulously scrutinised and its coherence with your committee stance will also be taken into account. This is mostly everything that I could think about, which was necessary to be briefed to all of you. Wishing you all the very best and I look forward to seeing all of you in committee vigorously scribbling something or the other on notepads or aggressively typing on your phones and asking hard to answer questions to any and every one!
      </p>
    <p>Thank you,<br>
      Regards,<br>
      Riddhiman Gangopadhyay,<br>
      Press Head of the International Press Corps,<br>
    The Ninth Secretariat,<br>
    Bosco MUN 2024</p>
