    <p class="address-section"><strong class="highlight-underline">About the Committee-</strong></p>
    <p>In Southeast Asia's vibrant landscape, the sudden shift in the affairs of Myanmar demands immediate attention. Following a sudden military coup that dismantled its fragile democratic framework, Myanmar plunged into upheaval and disarray. The crisis was marked by chaotic protests, severe military subjugation, and multifaceted international responses, which have thrust the nation to the brink of destabilization.
