      </p>
    <p>Om Arya Paul is a 12th grader humanities student who has a deep-rooted passion for geopolitics and foreign policy as well as international law along with a keen interest in law, economics, history, and political science. He is also a cinephile, who is always either researching on an intriguing topic or debating on a certain issue. 
    </p>
    <p>Pavvit Singh Batra is a 12th grade science student with an avid interest in Mathematics and Computer Science. He is serving as the tech team head for Bosco MUN 2024 and though he may come across as an introvert, he is one of the fiercest orators you can come across, promising to be the most well researched member of this executive board. What we, as your Executive Board, regard most importantly in committee are policy and rationality. While knowledge and research, too, are of paramount importance, they need to be articulated coherently, and presented spontaneously. Having spoken from both sides of the dais numerous times in the past, we can empathise with your worries, and would like all of you to feel free to contact any of us if you have any queries. The study guide is intended to serve only as the starting point for your research, so please do not let this guide limit your research. Now, let's get this show on the road and make Myanmar's situation less dramatic than a Netflix series finale. Thank you, and let's have a productive session! Happy researching, and good luck for the committee!
